@charset "UTF-8";
:root {
  --black: #0000009a;
  --white: #ffffff;
  --silver: #c8c8c89a;
  --dove-gray: #3b3b3b;
  --punch: #d92c2c;
  --seashell: #e7e2e2de;
  --tundora: #444444bb;
  --laurel: #007E06;
  --large: 26px;
  --medium: 22px;
  --small: 18px;
  --desktop-width: 1280px;
  --tablet-width: 748px;
  --large-mobile-width: 540px;
  --mobile-width: 320px;
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

img {
  display: block;
  max-width: 100%;
  height: 100%;
  object-fit: cover;
}

body {
  margin: 0;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.content-container {
  max-width: var(--desktop-width);
  margin: 0 auto;
  padding: 0 2rem;
}

.section-container {
  display: grid;
  gap: 2rem 0;
}

.header {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  z-index: 5;
  background-color: var(--seashell);
  box-shadow: 1px 2px 1px 0 var(--silver);
}
@media (max-width: 768px) {
  .header {
    box-shadow: none;
  }
}
.header__container {
  display: flex;
  align-items: center;
}
@media (max-width: 768px) {
  .header__container {
    justify-content: space-between;
  }
}
.header__navigation {
  margin-left: auto;
}
.header__button {
  background-color: transparent;
  border: none;
  padding: 0;
  font-family: inherit;
  display: none;
  position: relative;
  width: 35px;
  height: 35px;
  cursor: pointer;
}
.header__button::before {
  content: "";
  width: 100%;
  height: 2px;
  background-color: var(--black);
  position: absolute;
  top: 10%;
  left: 0;
  box-shadow: 0 13px 0 0px var(--black), 0 26px 0 0px var(--black);
  transition: transform 0.4s ease-in-out, box-shadow 0.1s ease-in;
}
.header__button--opened::before {
  box-shadow: none;
  transform: rotate(45deg);
  top: 50%;
  left: 0;
}
.header__button--opened::after {
  content: "";
  width: 100%;
  height: 2px;
  background-color: var(--black);
  position: absolute;
  bottom: 40%;
  left: 0;
  transform: rotate(-45deg);
  transition: transform 0.4s ease-in-out;
}
@media (max-width: 768px) {
  .header__button {
    display: block;
  }
}

.navigation {
  display: block;
}
@media (max-width: 768px) {
  .navigation {
    position: absolute;
    background-color: var(--seashell);
    bottom: -244px;
    left: 0;
    right: 0;
  }
}
@media (max-width: 768px) {
  .navigation--closed {
    display: none;
  }
}
.navigation__list {
  list-style-type: none;
  margin: 0;
  padding-left: 0;
  display: flex;
  align-items: center;
  gap: 1rem;
}
@media (max-width: 768px) {
  .navigation__list {
    flex-flow: column;
    gap: 2rem;
    padding: 2rem 0;
  }
}
.navigation__item {
  text-align: center;
  position: relative;
}
.navigation__link {
  padding: 1rem 3.2rem;
  text-decoration: none;
  text-transform: uppercase;
  color: var(--dove-gray);
  font-size: var(--medium);
  transition: background-color 0.2s ease-in-out;
}
@media (max-width: 1096px) {
  .navigation__link {
    padding: 1rem 2.2em;
    font-size: var(--small);
  }
}
@media (max-width: 898px) {
  .navigation__link {
    padding: 1.2rem;
    font-size: var(--small);
  }
}
.navigation__link:hover {
  background-color: var(--silver);
}

.popup {
  position: fixed;
  z-index: 10;
  top: 0;
  left: 0;
  right: 0;
  display: none;
}
.popup--active {
  background-color: var(--laurel);
}
.popup--disabled {
  background-color: var(--punch);
}
.popup--visible {
  display: block;
}
.popup__container {
  padding: 0.5rem 0;
  display: flex;
  justify-content: space-evenly;
  align-items: center;
}
.popup__message {
  margin: 0;
  font-size: var(--medium);
  color: var(--white);
  text-align: center;
}
@media (max-width: 768px) {
  .popup__message {
    font-size: var(--small);
  }
}
.popup__button {
  cursor: pointer;
  width: 25px;
  height: 25px;
  background-color: var(--white);
  border: 1px solid var(--tundora);
  transition: background-color 0.2s ease-in-out;
}
.popup__button:hover {
  background-color: var(--punch);
}

.scroll {
  position: fixed;
  left: 5%;
  bottom: 10%;
  width: 50px;
  height: 50px;
  border-radius: 50%;
  cursor: pointer;
  opacity: 0;
  visibility: hidden;
  box-shadow: 1px 2px 1px 0 var(--silver);
  transition: opacity 0.25s ease-in-out;
  color: var(--tundora);
  background-color: var(--seashell);
  border: 2px solid var(--tundora);
  transition: background-color 0.2s ease-in-out;
}
.scroll:hover {
  background-color: var(--silver);
}
.scroll:hover::before {
  top: 35%;
}
@media (max-width: 540px) {
  .scroll {
    width: 35px;
    height: 35px;
    left: 2%;
  }
}
.scroll::before {
  content: "â†‘";
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  font-size: var(--medium);
  transition: all 0.2s ease-in-out;
}
.scroll--active {
  opacity: 1;
  visibility: visible;
}

.title {
  margin: 0;
  font-size: var(--large);
  justify-self: center;
}

.main {
  display: grid;
  gap: 4rem 0;
  padding: 4rem 0 2rem;
  background: var(--seashell);
  font-family: "Lora", serif;
  position: relative;
}

.table__list {
  list-style-type: none;
  margin: 0;
  padding-left: 0;
  padding: 1rem;
  border: 1px solid var(--silver);
  box-shadow: 1px 2px 1px 0 var(--silver);
  display: grid;
  gap: 0 2rem;
  grid-template-columns: repeat(3, 1fr);
}
.table__item {
  text-align: center;
}
.table__column {
  list-style-type: none;
  margin: 0;
  padding-left: 0;
  display: grid;
  gap: 1rem 0;
  margin-top: 2rem;
}
.table__row-item {
  position: relative;
  border: 1px solid var(--tundora);
  padding: 1.5rem;
  cursor: pointer;
}
.table__row-item:hover > span {
  visibility: visible;
  opacity: 1;
}
.table__row-item > span {
  width: 150px;
  visibility: hidden;
  opacity: 0;
  padding: 0.5rem;
  position: absolute;
  top: 50%;
  right: -45%;
  transform: translateY(-50%);
  background-color: var(--silver);
  transition: opacity 0.25s ease-in-out;
  border-radius: 8px;
}
.table__row-item > span::before {
  content: "";
  display: inline-block;
  position: absolute;
  top: 40%;
  left: -5%;
  width: 15px;
  height: 15px;
  border-top: 2px solid var(--silver);
  border-right: 2px solid var(--silver);
  transform: rotate(-135deg);
}
.table__row-item > span > ul {
  list-style-type: none;
  margin: 0;
  padding-left: 0;
}
.table__row-item > span > ul > li {
  text-align: left;
}
.table__row-item:nth-child(1n):not(:last-child) {
  margin-bottom: 4rem;
}

.maps__list {
  list-style-type: none;
  margin: 0;
  padding-left: 0;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 1rem;
  max-height: 648px;
  overflow-y: scroll;
}
@media (max-width: 980px) {
  .maps__list {
    grid-template-columns: repeat(2, 1fr);
  }
}
@media (max-width: 620px) {
  .maps__list {
    grid-template-columns: 1fr;
  }
}
.maps__image {
  transition: scale 0.3s ease-in-out;
}
.maps__image-description {
  font-size: var(--large);
  font-weight: 700;
  color: var(--white);
  opacity: 0;
  background-color: var(--black);
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  transition: opacity 0.15s ease-out;
}
.maps__image-container {
  margin: 0;
  overflow: hidden;
  border-radius: 5%;
  position: relative;
}
.maps__image-container:hover > picture > img {
  scale: 1.1;
}
.maps__image-container:hover > figcaption {
  opacity: 1;
}

.rules__list {
  list-style-type: none;
  margin: 0;
  padding-left: 0;
  display: grid;
  justify-content: center;
  align-items: flex-start;
  gap: 10px 0;
}
.rules__item {
  border: 1px solid var(--silver);
  border-radius: 8px;
  box-shadow: 1px 2px 1px 0 var(--silver);
  min-width: 650px;
  max-width: 650px;
}
@media (max-width: 768px) {
  .rules__item {
    min-width: 480px;
    max-width: 480px;
  }
}
@media (max-width: 570px) {
  .rules__item {
    min-width: 360px;
    max-width: 360px;
  }
}
@media (max-width: 470px) {
  .rules__item {
    min-width: 230px;
    max-width: 230px;
  }
}
.rules__title {
  position: relative;
  display: block;
  padding: 1rem 9rem 1rem 1rem;
  font-size: var(--medium);
  cursor: pointer;
  transition: background-color 0.2s ease-in-out;
}
.rules__title::after {
  content: "";
  display: block;
  width: 20px;
  height: 20px;
  position: absolute;
  top: 50%;
  right: 10%;
  transform: translateY(-50%);
  background-image: url(../images/rules/arrow.svg);
  background-repeat: no-repeat;
  background-position: center;
  transition: transform 0.25s ease-in-out;
  transform-origin: center;
}
.rules__title:hover {
  background-color: var(--silver);
}
@media (max-width: 768px) {
  .rules__title {
    padding: 1rem 6rem 1rem 1rem;
  }
}
@media (max-width: 570px) {
  .rules__title {
    padding: 1rem 3.5rem 1rem 1rem;
  }
}
@media (max-width: 470px) {
  .rules__title {
    padding: 1rem 2.6rem 1rem 1rem;
  }
}
.rules__input:checked ~ .rules__content {
  display: block;
}
.rules__input:checked ~ .rules__title::after {
  top: 35%;
  transform: rotate(180deg);
}
.rules__input:focus-visible ~ .rules__title {
  outline: 1px solid var(--tundora);
}
.rules__content {
  display: none;
  margin: 0;
  padding: 0.5rem 1rem;
  font-size: var(--small);
  line-height: 1.4;
  border-top: 1px dashed var(--tundora);
}

.form {
  justify-self: center;
}
@media (max-width: 980px) {
  .form > iframe {
    max-width: 540px;
  }
}
@media (max-width: 570px) {
  .form > iframe {
    max-width: 420px;
  }
}
@media (max-width: 470px) {
  .form > iframe {
    max-width: 240px;
  }
}