.header {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  z-index: 5;
  background-color: var(--seashell);
  box-shadow: 1px 2px 1px 0 var(--silver);

  @include media(768px) {
    box-shadow: none;
  }

  &__container {
    display: flex;
    align-items: center;

    @include media(768px) {
      justify-content: space-between;
    }
  }

  &__navigation {
    margin-left: auto;
  }

  &__button {

    @include button-reset;
    display: none;
    position: relative;
    width: 35px;
    height: 35px;
    cursor: pointer;

    &::before {
      content: "";
      width: 100%;
      height: 2px;
      background-color: var(--black);
      position: absolute;
      top: 10%;
      left: 0;
      box-shadow: 0 13px 0 0px var(--black), 0 26px 0 0px var(--black);
      transition: transform 0.4s ease-in-out, box-shadow 0.1s ease-in;
    }

    &--opened {
      &::before {
        box-shadow: none;
        transform: rotate(45deg);
        top: 50%;
        left: 0;
      }

      &::after {
        content: "";
        width: 100%;
        height: 2px;
        background-color: var(--black);
        position: absolute;
        bottom: 40%;
        left: 0;
        transform: rotate(-45deg);
        transition: transform 0.4s ease-in-out;
      }
    }

    @include media(768px) {
      display: block;
    }
  }
}