.table {
  &__list {
    @include list-reset;
    padding: 1rem;
    border: 1px solid var(--silver);
    box-shadow: 1px 2px 1px 0 var(--silver);
    display: grid;
    gap: 0 2rem;
    grid-template-columns: repeat(3, 1fr);
  }

  &__item {
    text-align: center;
  }

  &__column {
    @include list-reset;
    display: grid;
    gap: 1rem 0;
    margin-top: 2rem;
  }

  &__row-item {
    position: relative;
    border: 1px solid var(--tundora);
    padding: 1.5rem;
    cursor: pointer;

    &:hover > span {
      visibility: visible;
      opacity: 1;
    }

    & > span {
      width: 150px;
      visibility: hidden;
      opacity: 0;
      padding: 0.5rem;
      position: absolute;
      top: 50%;
      right: -45%;
      transform: translateY(-50%);
      background-color: var(--silver);
      transition: opacity 0.25s ease-in-out;
      border-radius: 8px;

      &::before {
        content: "";
        display: inline-block;
        position: absolute;
        top: 40%;
        left: -5%;
        width: 15px;
        height: 15px;
        border-top: 2px solid var(--silver);
        border-right: 2px solid var(--silver);
        transform: rotate(-135deg);
      }

      & > ul {
        @include list-reset;

        & > li {
          text-align: left;
        }
      }
    }

    &:nth-child(1n):not(:last-child) {
      margin-bottom: 4rem;
    }
  }
}
