.navigation {
  display: block;

  @include media(768px) {
    position: absolute;
    background-color: var(--seashell);
    bottom: -244px;
    left: 0;
    right: 0;
  }

  &--closed {
    @include media(768px) {
      display: none
    }
  }

  &__list {
    @include list-reset;
    display: flex;
    align-items: center;
    gap: 1rem;

    @include media(768px) {
      flex-flow: column;
      gap: 2rem;
      padding: 2rem 0;
    }
  }

  &__item {
    text-align: center;
    position: relative;
  }

  &__link {
    padding: 1rem 3.2rem;
    text-decoration: none;
    text-transform: uppercase;
    color: var(--dove-gray);
    font-size: var(--medium);
    transition: background-color 0.2s ease-in-out;

    @include media(1096px) {
      padding: 1rem 2.2em;
      font-size: var(--small);
    }

    @include media(898px) {
      padding: 1.2rem;
      font-size: var(--small);
    }

    &:hover {
      background-color: var(--silver);
    }
  }
}
